.logo {
	outline: transparent;
	transition: all 0.3s ease 0s;
	border-radius: 50%;

	&:focus {
		box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
	}

	@media (any-hover: hover) {
		&:hover {
			opacity: 0.8;
		}
	}
}

.social-list {
	display: flex;

	&__item {
		&:not(:last-child) {
			margin-right: 25px;
		}
	}

	&__link {
		display: flex;
		justify-content: center;
		align-items: center;
		outline: transparent;

		&:focus {
			svg {
				fill: var(--pink);
			}
		}

		@media (any-hover: hover) {
			&:hover {
				svg {
					fill: var(--dark-grey-color);
				}
			}
		}

		svg {
			transition: fill 0.3s ease 0s;
			width: 24px;
			height: 24px;
			fill: var(--white-color);
		}
	}
}

.switch-color {
	display: flex;
	justify-content: center;
	align-items: center;

	svg {
		width: 24px;
		height: 24px;
		fill: var(--white-color);
		transition: fill 0.3s ease 0s;
	}

	&:focus {
		svg {
			fill: var(--pink);
		}
	}

	@media (any-hover: hover) {
		&:hover {
			svg {
				fill: var(--dark-grey-color);
			}
		}
	}
}

.menu {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;

	@include mq("tablet", max) {
		background-color: var(--pink-light);
		flex-direction: column;
		padding: 110px 0px 80px 0px;
		margin-right: 0;
		overflow: auto;
		transform: translateY(-200%);
		transition: transform 0.4s;
		width: 100%;

		@include mq("phone", max) {
			padding: 90px 0 40px 0;
		}
	}

	&--active {
		transform: translateY(0);
		z-index: 2;

		& .header__search {
			display: block;
		}
	}

	&__item {
		@include mq("tablet", max) {
			width: 100%;
			text-align: center;
			font-size: 24px;
		}

		&:not(:last-child) {
			@include adaptiveValue("margin-right", 60, 20);

			@include mq("tablet", max) {
				margin-right: 0;
				margin-bottom: 20px;
			}
		}
	}

	&__link {
		transition: color 0.3s ease 0s;
		outline: transparent;

		&:focus {
			color: var(--red-hover);
		}

		@media (any-hover: hover) {
			&:hover {
				color: var(--accent-color);
			}
		}
	}
}

.text {
	font-family: "NunitoSans";
	line-height: 1.5;

	@include mq("phone", max) {
		font-size: 16px;
		line-height: 1.2;
	}
}

.recommendations {
	background-color: var(--pink-color);
	padding: 47px 0;

	&__item {
		display: flex;
		align-items: center;
		max-width: 270px;
		width: 100%;

		&:not(:last-child) {
			margin-right: 45px;
		}
	}

	&__img {
		margin-right: 15px;
	}

	&__content {
		font-family: "Montserrat";
	}

	&__span {
		display: block;
		font-size: 16px;
		margin-bottom: 5px;
	}

	&__num {
		font-weight: 500;
	}
}

.price-table {
	border-collapse: collapse;
	width: 100%;

	@include mq("tablet-s", max) {
		font-size: 13px;
	}

	th {
		padding: 30px 0;

		@include mq("tablet-s", max) {
			padding: 20px 0;
		}
	}

	&__title {
		display: inline-block;
		font-family: "OpenSans";
		font-weight: 400;
		font-size: 24px;
		line-height: 1.2;
		margin-bottom: 15px;
		text-align: left;
		position: relative;
		padding-right: 35px;

		svg {
			fill: var(--black-color);
			transform: rotate(180deg);
			height: 18px;
			width: 21px;
		}

		@include mq("phone", max) {
			font-size: 18px;
		}
	}

	&__line {
		border-bottom: 1px solid var(--grey-border);
		display: flex;
		justify-content: space-between;
		align-items: center;

		&:first-child {
			border-top: 1px solid var(--grey-border);
		}
	}

	&__name {
		font-family: "SourceSansPro";
		font-weight: 400;
		line-height: 1.5;
		text-align: left;
		max-width: 645px;
		width: 100%;
		margin-right: 10px;

		@include mq("phone", max) {
			margin-right: 7px;
		}
	}

	&__time,
	&__price {
		margin-right: 10px;
		flex-shrink: 0;

		@include mq("phone", max) {
			flex-shrink: 1;
			margin-right: 7px;
		}
	}

	&__time {
		font-weight: 400;
	}

	&__link {
		padding: 13px 40px;

		@include mq("phone", max) {
			padding: 8px 10px;
			font-size: 11px;
		}
	}
}

.pockets {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 25px;

	@include mq("desktop", max) {
		grid-template-columns: repeat(2, 1fr);
	}

	@include mq("tablet-s", max) {
		grid-template-columns: repeat(1, 1fr);
		gap-row: 20px;
	}
}

.stocks {
	display: flex;
	justify-content: space-between;

	@include mq("tablet", max) {
		flex-direction: column;
	}

	&__item {
		max-width: 455px;
		width: 100%;

		@include mq("tablet", max) {
			max-width: 100%;
		}

		&:not(:last-child) {
			margin-right: 15px;

			@include mq("tablet", max) {
				margin: 0 0 30px 0;
			}
		}
	}
}

.advantages {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;

	@include mq("desktop", max) {
		justify-content: center;
	}

	&__item {
		max-width: 265px;
		width: 100%;
		text-align: center;
		margin: 0 15px 40px 15px;

		@include mq("phone", max) {
			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	&__box {
		background-color: var(--pink-light);
		border-radius: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 88px;
		height: 88px;
		margin: 0 auto 15px auto;
	}

	&__name {
		font-size: 20px;
		margin-bottom: 5px;

		@include mq("phone", max) {
			font-size: 18px;
		}
	}

	&__descr {
		font-size: 16px;
	}
}

.slider-btns {
	display: flex;

	&__btn {
		width: 55px;

		&:not(:last-child) {
			margin-right: 15px;
		}
	}
}

.accordion {
	&__content {
		width: 100%;
		max-height: 0;
		overflow: hidden;
		will-change: max-height;
		transition: overflow 0.4s, max-height 0.4s ease-out;
	}

	.open .accordion__content {
		overflow: visible;
		margin-bottom: 20px;
	}

	.open .accordion__btn {
		&::after {
			transform: rotate(180deg);

			@include mq("phone-s", max) {
				transform: rotate(180deg) translateY(75%);
			}
		}
	}

	.open .accordion__btn {
		color: var(--accent-color);
	}

	&--white {
		.accordion__item {
			&:not(:last-child) {
				margin-bottom: 15px;
			}
		}

		.accordion__btn {
			background-color: var(--white-color);
			padding: 25px 30px;
			position: relative;
			padding-right: 85px;
			width: 100%;
			transition: color 0.2s ease 0s;

			@media (any-hover: hover) {
				&:hover {
					color: var(--accent-color);
				}
			}

			@include mq("phone", max) {
				padding: 15px 35px 15px 20px;
			}

			&:after {
				content: "";
				display: block;
				width: 24px;
				height: 24px;
				background-color: var(--accent-color);
				background-image: url(../images/icons/icon-arrow-up.svg);
				background-repeat: no-repeat;
				background-position: center;
				background-size: 10px 13px;
				border-radius: 50%;
				transition: transform 0.3s;
				position: absolute;
				right: 32px;
				top: 30px;

				@include mq("phone", max) {
					right: 22px;
					top: 50%;
					transform: translateY(-50%);
				}
			}
		}

		.accordion__name {
			display: block;
			font-size: 18px;
			font-weight: 500;
			text-align: left;
			max-width: 550px;
			width: 100%;

			@include mq("phone", max) {
				font-size: 17px;
				line-height: 1.2;
			}
		}

		.accordion__content {
			max-width: 700px;
			width: 100%;

			@include mq("desktop-s", max) {
				max-width: 100%;
			}

			p {
				background-color: var(--white-color);
				font-family: "SourceSansPro";
				font-weight: 300;
				padding: 0 30px 25px 30px;

				@include mq("phone", max) {
					padding: 0 20px 15px 20px;
				}
			}
		}
	}
}

.play-btn {
	display: block;
	background-color: var(--accent-color);
	border-radius: 50%;
	width: 50px;
	height: 50px;
	position: relative;
	transition: background-color 0.3s ease 0s;

	@media (any-hover: hover) {
		&:hover {
			background-color: var(--red-hover);
		}
	}

	svg {
		fill: var(--bg-color);
		width: 22px;
		height: 22px;
		position: absolute;
		top: 50%;
		left: 54%;
		transform: translate(-50%, -54%);
	}
}
